/* MODALI STYLE */
.modali-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: red;
  opacity: 0.5;
}

.modali-wrapper {
  display: inline-table;
  position: fixed;
  padding: 0;
  top: 0;
  left: 0;
  z-index: 1050;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  outline: 0;
}

.modali {
  z-index: 100;
  background: black;
  position: relative;
  margin: 1.75rem auto;
  border-radius: 0;
  pointer-events: none;
}

.modali-content {
  pointer-events: auto;
}

.modali-header {
  display: flex;
  align-items: flex-start;
  padding: 0;
}

.modali-header .modali-title {
  font-weight: 700;
  font-size: 1.2rem;
}

.modali-close-button {
  padding: 1rem;
  background: white;
  margin: -1rem -1rem -1rem auto;
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 1;
  color: #000;
  opacity: 0.3;
  cursor: pointer;
  border: none;
  outline: 0 !important;
}

.modali-close-button:hover {
  opacity: 0.8;
  text-decoration: none;
}

.modali-body {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: black;
  border-radius: 0;
}

.modali-body-style {
  padding: 0.5rem 1.5rem;
}

.modali-footer {
  padding: 1rem 1.5rem;
  display: flex;
  justify-content: flex-end;
}

.modali-open {
  overflow: hidden;
}

/* Size Classes */

.modali-size-regular {
  min-width: 100%;
  max-width: 100vw;
}

.modali-size-large {
  min-width: 100%;
  max-width: 800px;
}

/* Position Classes */

.modali-wrapper-centered {
  top: 40% !important;
}

/* Button Classes */

.modali-button {
  font-size: 0.9rem;
  font-weight: 700;
  border: none;
  border-radius: 3px;
  padding: 0.3rem 1rem;
  margin-left: 0.5rem;
}

.modali-button-cancel {
  background: #fff;
  color: #000;
}

.modali-button-destructive {
  background: #ff1654;
  color: #fff;
}

.modali-button-default {
  background: #247ba0;
  color: #fff;
}

/* Animation Classes */

.modali-animated {
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.modali-animation-fade-in {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 30%, 0);
    transform: translate3d(0, 30%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 30%, 0);
    transform: translate3d(0, 30%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

