  width: 100%;
  background-color: black;
  border-radius: 0;
}

.modali-body-style {
  padding: 0;
}

.modali-footer {
  padding: 0;
  display: flex;
  justify-content: flex-end;
}

.modali-open {
  overflow: hidden;
}

/* Size Classes */

.modali-size-regular {
  min-width: 100%;
  max-width: 100vw;
}

.modali-size-large {
  min-width: 100%;
  max-width: 800px;
}

/* Position Classes */

.modali-wrapper-centered {
  top: 0 !important;
}

/* Button Classes */

.modali-button {
  font-size: 0.9rem;
  font-weight: 700;
  border: none;
  border-radius: 3px;
  padding: 0;
  margin-left: 0;
}

.modali-button-cancel {
  background: #fff;
  color: #000;
}

.modali-button-destructive {
  background: #ff1654;
  color: #fff;
}

.modali-button-default {
  background: #247ba0;
  color: #fff;
}

/* Animation Classes */

.modali-animated {
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.modali-animation-fade-in {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 30%, 0);
    transform: translate3d(0, 30%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 30%, 0);
    transform: translate3d(0, 30%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

